<template>
    <d2-container>
        <!-- 商品类别管理 -->
        <!-- header -->
        <template slot="header">
            <el-row>
                <el-col :span="3" style="margin-right:10px;">
                    <!-- <el-autocomplete class="inline-input" v-model="state1" :fetch-suggestions="querySearch" placeholder="请输入内容"></el-autocomplete> -->
                    <el-alert title="商品类别管理" type="info" :closable="false">
                    </el-alert>
                </el-col>
                <el-col :span="5">
                    <!-- <el-button type="primary" plain>查询</el-button> -->
                    <el-button type="primary" plain @click="add_good">添加</el-button>
                </el-col>
            </el-row>
        </template>
        <el-table :data="tableData" border style="width: 100%">
           
            <el-table-column type="index" width="50">
            </el-table-column>
            <el-table-column prop="goods_type_name" label="商品类别名称" width="180">
            </el-table-column>
            <el-table-column prop="goods_type_desc" label="商品类别描述" width="180">
            </el-table-column>
            <el-table-column prop="goods_type_leaset" label="起购价--单位￥">
            </el-table-column>
            <el-table-column label="操作" width="280">
                <template scope="val">
                    <el-button type="primary" plain @click="edit_type(val.row)">编辑</el-button>
                    <!-- <el-button type="primary" plain>修改</el-button> -->
                    <el-button type="danger" plain @click="del_val(val.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- footer -->
        <el-row type="flex" justify="space-between" align="middle" style="height: 80px;">
            <el-col :span="24">
                <el-pagination layout="prev, pager, next" @current-change="page" :page-size="10" :total="checked" style="float: right;">
                </el-pagination>
            </el-col>
        </el-row>


        <!-- 添加商品类别管理 -->
        <el-dialog :title="num" :visible.sync="centerDialogVisible" width="30%" center :before-close="clear">
            <el-form ref="form" :model="form" label-width="100px">
                <el-form-item label="商品类别名称">
                    <el-input v-model="form.goods_type_name" placeholder="请输入商品类别名称"></el-input>
                </el-form-item>
                <el-form-item label="商品类别描述">
                    <el-input v-model="form.goods_type_desc" placeholder="请输入商品类别描述"></el-input>
                </el-form-item>
                <el-form-item label="起购价">
                    <el-input placeholder="请输入起购价" v-model="form.goods_type_leaset">
                    </el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="clear">取 消</el-button>
                <el-button type="primary" @click="onSubmit" v-if="num!=='编辑商品类别管理'">确 定</el-button>
                <el-button type="primary" @click="onSubmit_type" v-if="num=='编辑商品类别管理'">确 定</el-button>
            </span>
        </el-dialog>

    </d2-container>

</template>

<script>
    import { add_goods_type, show_goods_type, edit_goods_type, del_goods_type } from '../../plugin/axios/api.js';
    import { Myerror } from '../../plugin/axios/er.js';
    import qs from 'qs';
    export default {
        name: 'GoodStyle_home',
        data() {
            return {
                num: '',
                value3: '',
                state1: '',
                checked: 0,
                tableData: [],
                dialogVisible: false,
                pages: 1,
                centerDialogVisible: false,
                form: {
                    goods_type_name: '',
                    goods_type_desc: '',
                    goods_type_leaset: '',
                },
            }
        },
        methods: {
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                // 调用 callback 返回建议列表的数据
                cb(results);
            },
            createFilter(queryString) {
                return (restaurant) => {
                    return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
                };
            },
            add_good() {//添加商品
                this.num = "添加商品类别管理";
                this.centerDialogVisible = true;
            },
            onSubmit() {//添加商品
                let par = qs.stringify(this.form)
                add_goods_type(par).then(res => {
                    this.msg(res)
                })
            },
            edit_type(a) {//编辑商品
                this.num = "编辑商品类别管理";
                this.form = {
                    goods_type_name: a.goods_type_name,
                    goods_type_desc: a.goods_type_desc,
                    goods_type_leaset: a.goods_type_leaset,
                    id: a.id
                }
                this.centerDialogVisible = true;
            },
            onSubmit_type(){//编辑商品
                let par = {
                    goods_type_name: this.form.goods_type_name,
                    goods_type_desc: this.form.goods_type_desc,
                    goods_type_leaset: this.form.goods_type_leaset, 
                }
                let id = this.form.id;
                edit_goods_type(par,id).then(res=>{
                    this.msg(res)
                })
            },
            del_val(a) {//删除商品
                console.log(a);
                del_goods_type(a.id).then(res => {
                    this.msg(res)
                })
            },
            page(a) {
                console.log(a);
                this.pages = a;
                this.init_goods()
            },
            init_goods() {
                let par = {
                    page:this.pages,
                    limit:10
                }
                show_goods_type(par).then(res => {
                    this.tableData = res.data.data;
                    this.checked = res.data.paginate.total;
                })
            },
            msg(res) {//返回参数处理
                if (res.code) {
                    this.$message({
                        message: res.message,
                        center: true,
                        type: 'success'
                    });
                    this.clear()
                    this.init_goods()

                    return
                }
                Myerror(res, this)
            },
            clear() {
                this.centerDialogVisible = false;
                this.form = {
                    goods_type_name: '',
                    goods_type_desc: '',
                    goods_type_leaset: '',
                }

            }
        },
        mounted() {
            this.init_goods()
        }
    }
</script>
<style lang="scss">
</style>